import { FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs';
import { Component, Directive, Output, EventEmitter, Input, OnInit } from '@angular/core';
import {
  CategoriesService,
  CategoryItem,
  TransactionUpdateService,
  Transaction,
} from '@backbase/transactions-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-transactions-categories',
  templateUrl: 'transactions-categories.component.html',
})
export class TransactionsCategoriesComponent implements OnInit {
  @Input() transaction: Transaction | undefined;
  @Output() readonly categoryChange = new EventEmitter<string>();

  readonly changeTransactionsCategoryForm = this.fb.group({
    categoryName: [],
  });

  readonly hostRef = this;
  readonly categoriesList: Observable<CategoryItem[]> = this.categoriesService.getCategories();

  constructor(
    private readonly fb: FormBuilder,
    private readonly categoriesService: CategoriesService,
    private readonly transactionUpdateService: TransactionUpdateService,
  ) {}

  cancelSaveCategory() {
    this.changeTransactionsCategoryForm.reset({
      categoryName: this.transaction && this.transaction.category,
    });
    this.categoryChange.emit();
  }

  changeCategory() {
    const newCategoryControl = this.changeTransactionsCategoryForm.get('categoryName');

    if (this.transaction && newCategoryControl) {
      this.transactionUpdateService.update(this.transaction, { category: newCategoryControl.value }).subscribe(
        () => {
          if (this.transaction) {
            this.transaction.category = newCategoryControl.value;
            this.categoryChange.emit('success');
          } else {
            this.categoryChange.emit('error');
          }
        },
        () => this.categoryChange.emit('error'),
      );
    }
  }

  getCategoryIconClass(transactionCategory: string): string {
    return `${transactionCategory.toLowerCase().replace(/\W/g, '-').replace(/-{2,}/g, '-') || 'uncategorized'}`;
  }

  ngOnInit() {
    this.changeTransactionsCategoryForm.reset({
      categoryName: this.transaction && this.transaction.category,
    });
  }
}

@Directive({
  selector: '[bbTransactionsCategoryCustomizable]',
})
export class TransactionsCategoriesCustomizableDirective extends BbTemplate<
  TransactionsCategoriesComponent,
  undefined
> {}
