import { Component, Input, Directive, OnInit, Output, EventEmitter } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  InquireAndDisputeTopics,
  Transaction,
  TransactionCheckImageResponse,
  TransactionsDetailsLoading,
} from '@backbase/transactions-common-ang';

@Component({
  selector: 'bb-transactions-list-item-group',
  templateUrl: './transactions-list-item-group.component.html',
})
export class TransactionsListItemGroupComponent implements OnInit {
  /**
   * Title to be displayed over transactions.
   */
  @Input() transactionDate: string | undefined;
  /**
   * The transactions this component shows.
   */
  @Input() transactions: Transaction[] | undefined;
  /**
   * Boolean that decides if the Change Category will be displayed.
   */
  @Input() showChangeCategory: boolean | undefined;

  @Input() checkImagesList: TransactionCheckImageResponse | undefined;
  @Input() transactionDetailsLoadingStatus: TransactionsDetailsLoading | undefined;
  @Input() inquireAndDisputeTopics: InquireAndDisputeTopics | undefined;
  @Output() readonly detailsOpen = new EventEmitter<Transaction>();

  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;

  ngOnInit() {
    if (typeof this.transactions === 'undefined') {
      throw new Error(`"transactions" input is required in "${this.constructor.name}"`);
    }
  }

  get daysElapsed(): number | undefined {
    if (this.transactionDate === undefined) {
      return undefined;
    }

    let transactionDate = new Date(this.transactionDate);

    if (Number.isNaN(transactionDate.getTime())) {
      return undefined;
    }

    const millisecondsPerDay = 86400000;
    const today = this.roundDate(new Date());
    transactionDate = this.roundDate(transactionDate);

    return Math.floor((today.getTime() - transactionDate.getTime()) / millisecondsPerDay);
  }

  onDetailOpen(transaction: Transaction) {
    this.detailsOpen.emit(transaction);
  }

  private roundDate(date: Date): Date {
    date.setUTCHours(0);
    date.setUTCMinutes(0);
    date.setUTCSeconds(0);

    return date;
  }

  trackByFn(_index: number, element: Transaction) {
    return element.id;
  }
}

@Directive({
  selector: '[bbTransactionsListItemGroupCustomizable]',
})
export class TransactionsListItemGroupCustomizableDirective extends BbTemplate<
  TransactionsListItemGroupComponent,
  undefined
> {}
