import { Component, Input, Directive, Output, EventEmitter, OnInit } from '@angular/core';
import {
  Transaction,
  TransactionsIconCategoriesFactoryService,
  BaseTransactionIcon,
  TransactionIconType,
} from '@backbase/transactions-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-transaction-detail-category',
  templateUrl: 'transaction-detail-category.component.html',
})
export class TransactionDetailCategoryComponent implements OnInit {
  @Input() transaction: Transaction | undefined;
  @Output() readonly categoryChange = new EventEmitter<string>();
  /**
   * Boolean that decides if the Change Category will be displayed.
   */
  @Input() displayChangeCategory: boolean | undefined;
  hostRef = this;

  isTransactionsCategoryOpen = false;
  public transactionCategoryIcon: BaseTransactionIcon | undefined;
  transactionIconType = TransactionIconType;

  ngOnInit() {
    if (typeof this.transaction === 'undefined') {
      throw new Error(`"transaction" input is required in "${this.constructor.name}"`);
    }
    this.transactionCategoryIcon = this.transactionsIconCategoriesFactory.create(this.transaction);
  }

  get categoryTitle() {
    if (this.transactionCategoryIcon) {
      return this.transactionCategoryIcon.title;
    }

    return '';
  }

  openTransactionsCategory() {
    this.isTransactionsCategoryOpen = true;
  }

  closeTransactionsCategory(categoryChangeStatus?: string) {
    this.categoryChange.emit(categoryChangeStatus);
    this.isTransactionsCategoryOpen = false;
  }

  constructor(private transactionsIconCategoriesFactory: TransactionsIconCategoriesFactoryService) {}
}

@Directive({
  selector: '[bbTransactionDetailCategoryCustomizable]',
})
export class TransactionDetailCategoryCustomizableDirective extends BbTemplate<
  TransactionDetailCategoryComponent,
  undefined
> {}
