import {
  AfterViewInit,
  Component,
  Directive,
  EventEmitter,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  Optional,
  Output,
  SimpleChanges,
} from '@angular/core';
import { BbTemplate, FeaturesService } from '@backbase/foundation-ang/core';
import {
  InquireAndDisputeTopics,
  LoadingState,
  PaginationType,
  showDetailsConfigToken,
  Transaction,
  TransactionCheckImageResponse,
  TransactionsDetailsLoading,
  TransactionsGroupedByDate,
  TransactionsList,
} from '@backbase/transactions-common-ang';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { TRANSACTIONS_DETAILS_SHARE_MODAL } from '../../constants';

@Component({
  selector: 'bb-transactions-list',
  templateUrl: './transactions-list.component.html',
})
export class TransactionsListComponent implements OnChanges, AfterViewInit, OnDestroy {
  /**
   * The TransactionsList this component displays the transactions of.
   */
  @Input() transactionsList: TransactionsList | undefined;
  /**
   * The current page of transactions that is being displayed.
   */
  @Input() currentPage: number | undefined;
  /**
   * The page size.
   */
  @Input() pageSize: number | undefined;
  /**
   * Whether new transactions are being loaded.  By default, this is `LoadingState.Loaded`.
   */
  @Input() loadingState: LoadingState = LoadingState.Loaded;
  /**
   * The maximum number of pages to show (only relevant when using pagination).
   */
  @Input() maxNavPages: number | undefined;
  /**
   * The pagination type (`load-more`, `pagination` or undefined).
   */
  @Input() paginationType: PaginationType | undefined;
  /**
   * Number of results found after filtering or searching.
   */
  @Input() numberOfResultsFound: number | undefined;
  /**
   * Boolean that decides if the Change Category will be displayed.
   */
  @Input() showChangeCategory: boolean | undefined;
  /**
   * Event emitter for loading the next page of transactions.
   */
  @Output() readonly loadMore = new EventEmitter();
  /**
   * Event emitter for loading a specific page of transactions.
   */
  @Output() readonly pageChange = new EventEmitter<number>();

  @Input() checkImagesList: TransactionCheckImageResponse | undefined;
  @Input() inquireAndDisputeTopics: InquireAndDisputeTopics | undefined;
  @Input() transactionDetailsLoadingStatus: TransactionsDetailsLoading | undefined;
  @Output() readonly detailsOpen = new EventEmitter<Transaction>();

  constructor(
    private readonly featureService: FeaturesService,
    @Optional()
    @Inject(showDetailsConfigToken)
    public showDetailsConfig: boolean,
  ) {
    if (this.showDetailsConfig === null || this.showDetailsConfig === undefined) {
      this.showDetailsConfig = true;
    }
  }
  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  /**
   * Whether the transaction details are shown or not.
   */
  isDetailsOpen = false;

  transactionsGroupedByDate: any[] | undefined;
  transaction: Transaction | undefined;

  private unsubscribeSubject$ = new Subject<void>();

  /**
   * Whether there are any loaded transactions.
   */
  hasItems() {
    if (this.numberOfResultsFound !== undefined) {
      return this.numberOfResultsFound > 0;
    }

    return this.transactionsGroupedByDate && this.transactionsGroupedByDate.length > 0;
  }

  get hasModal(): boolean {
    return this.featureService.isEnabled(TRANSACTIONS_DETAILS_SHARE_MODAL) && this.showDetailsConfig;
  }

  /**
   * Whether transactions are loading.
   */
  get isLoading() {
    return this.loadingState !== LoadingState.Loaded;
  }

  get isLoadingMore() {
    return this.loadingState === LoadingState.LoadingMore || this.loadingState === LoadingState.LoadingPage;
  }

  /**
   * Whether pagination is configured to be paged.
   */
  get isPaginationPaged() {
    return this.paginationType === PaginationType.PAGINATION;
  }

  /**
   * Whether pagination is configured to be a load-more button.
   */
  get isPaginationLoadMore() {
    return this.paginationType === PaginationType.LOAD_MORE;
  }

  /**
   * Whether pagination is configured to be a infinite-scroll.
   */
  get isInfiniteScrollPagination() {
    return this.paginationType === PaginationType.INFINITE_SCROLL;
  }

  /**
   * Returns whether all transactions have been loaded or not.
   */
  hasLoadedAll() {
    return this.transactionsList && TransactionsList.hasLoadedAll(this.transactionsList);
  }

  onLoadMore() {
    this.loadMore.emit();
  }

  onPageChange(page: number) {
    this.pageChange.emit(page);
  }

  onDetailOpen(transaction: Transaction) {
    this.isDetailsOpen = true;
    this.transaction = transaction;
    this.detailsOpen.emit(transaction);
  }

  /**
   * Hide the transaction details.
   */
  closeDetails() {
    this.isDetailsOpen = false;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.transactionsList) {
      this.transactionsGroupedByDate = Object.entries(TransactionsGroupedByDate.groupByDate(this.transactionsList));
    }
  }

  trackByFn(_index: number, group: Array<any>) {
    return group[0];
  }

  ngAfterViewInit(): void {
    if (this.isInfiniteScrollPagination) {
      fromEvent(window, 'scroll')
        .pipe(takeUntil(this.unsubscribeSubject$))
        .subscribe(() => {
          if (!(this.hasLoadedAll() || this.isLoading)) {
            const element = document.documentElement;
            const loadingOffsetHeight = element.offsetHeight * 1.5;
            if (loadingOffsetHeight + element.scrollTop >= element.scrollHeight) {
              this.onLoadMore();
            }
          }
        });
    }
  }

  ngOnDestroy(): void {
    this.unsubscribeSubject$.next();
    this.unsubscribeSubject$.complete();
  }
}

@Directive({
  selector: '[bbTransactionsListPaginationCustomizable]',
})
export class TransactionsListPaginationCustomizableDirective extends BbTemplate<TransactionsListComponent, undefined> {}

@Directive({
  selector: '[bbTransactionsListListCustomizable]',
})
export class TransactionsListListCustomizableDirective extends BbTemplate<TransactionsListComponent, undefined> {}

@Directive({
  selector: '[bbTransactionsListEmptyStateCustomizable]',
})
export class TransactionsListEmptyStateCustomizableDirective extends BbTemplate<TransactionsListComponent, undefined> {}
