import { ChangeDetectionStrategy, Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  InquireAndDisputeTopics,
  LoadingState,
  PaginationType,
  Transaction,
  TransactionsList,
} from '@backbase/transactions-common-ang';

@Component({
  selector: 'bb-pending-transactions-list',
  templateUrl: './transactions-list-pending-transactions.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsListPendingTransactionsComponent {
  /**
   * List of pending transactions.
   */
  @Input() transactionsList: TransactionsList | undefined;
  /**
   * whether to show pending transactions on top or not.
   */
  @Input() showPendingOnTop: boolean | undefined;
  /**
   * The current page of transactions that is being displayed.
   */
  @Input() currentPage: number | undefined;
  /**
   * The page size.
   */
  @Input() pageSize: number | undefined;
  /**
   * The pagination type (`load-more`, `pagination` or undefined).
   */
  @Input() paginationType: PaginationType | undefined;
  /**
   * Whether new transactions are being loaded.  By default, this is `LoadingState.Loaded`.
   */
  @Input() loadingState: LoadingState = LoadingState.Loaded;
  /**
   * The maximum number of pages to show (only relevant when using pagination).
   */
  @Input() maxNavPages: number | undefined;
  /**
   * Boolean that decides if the Change Category will be displayed.
   */
  @Input() showChangeCategory: boolean | undefined;
  @Input() inquireAndDisputeTopics: InquireAndDisputeTopics | undefined;

  /**
   * Event emitter that triggers when the details are open
   */
  @Output() readonly detailsOpen = new EventEmitter<Transaction>();
  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;

  get isLoading() {
    return this.loadingState !== LoadingState.Loaded;
  }

  paginationOnTop() {
    if (this.currentPage === 0) {
      return true;
    }
    if (this.currentPage && this.currentPage > 0) {
      if (this.paginationType === PaginationType.PAGINATION) {
        return false;
      }
      if (this.paginationType === PaginationType.LOAD_MORE || this.paginationType === PaginationType.INFINITE_SCROLL) {
        return true;
      }
    }

    return false;
  }

  onDetailOpen(transaction: Transaction) {
    this.detailsOpen.emit(transaction);
  }

  trackByFn(_index: number, element: Transaction) {
    return element.id;
  }
}

@Directive({
  selector: '[bbPendingTransactionsListCustomizable]',
})
export class PendingTransactionsListCustomizableDirective extends BbTemplate<
  TransactionsListPendingTransactionsComponent,
  undefined
> {}
