import { ChangeDetectionStrategy, Inject, Optional } from '@angular/core';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';
import {
  TransactionsService,
  WidgetPropertiesService,
  BaseTransactionsContainer,
  PendingTransactionsService,
  TransactionDetailsService,
  showDetailsConfigToken,
  AvailableFiltersConfigService,
  PaginationType,
} from '@backbase/transactions-common-ang';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-transactions-container',
  templateUrl: './transactions-container.component.html',
  providers: [TransactionsService, PendingTransactionsService, TransactionDetailsService],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsContainerComponent extends BaseTransactionsContainer implements OnInit {
  constructor(
    protected readonly model: TransactionsService,
    protected readonly widgetProperties: WidgetPropertiesService,
    protected readonly cd: ChangeDetectorRef,
    @Inject(PUBSUB) protected readonly eventBusService: Pubsub,
    protected readonly notificationService: NotificationService,
    protected readonly pendingTransactionsService: PendingTransactionsService,
    protected readonly transactionDetailsService: TransactionDetailsService,
    protected readonly availableFiltersConfigService: AvailableFiltersConfigService,
    // eslint-disable-next-line
    @Optional()
    // eslint-disable-next-line
    @Inject(showDetailsConfigToken)
    readonly showDetailsConfig?: boolean,
  ) {
    super(
      model,
      widgetProperties,
      cd,
      eventBusService,
      notificationService,
      pendingTransactionsService,
      transactionDetailsService,
      availableFiltersConfigService,
      showDetailsConfig,
    );

    if (this.showDetailsConfig === null || this.showDetailsConfig === undefined) {
      this.showDetailsConfig = true;
    }
  }

  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  readonly PaginationType = PaginationType;
}
