import { Component, Input, Directive, OnInit, Output, EventEmitter, Optional, Inject } from '@angular/core';
import { BbTemplate, FeaturesService } from '@backbase/foundation-ang/core';
import {
  Transaction,
  BillingStatusEnum,
  TransactionCheckImageResponse,
  TransactionsDetailsLoading,
  showDetailsConfigToken,
  InquireAndDisputeTopics,
} from '@backbase/transactions-common-ang';
import { TRANSACTIONS_DETAILS_SHARE_MODAL } from '../../constants';

@Component({
  selector: 'bb-transactions-list-item',
  templateUrl: './transactions-list-item.component.html',
})
export class TransactionsListItemComponent implements OnInit {
  /**
   * The transaction this component shows.
   */
  @Input() transaction: Transaction | undefined;
  /**
   * Whether to show pending transactions on top or not.
   */
  @Input() showBillingStatus = false;
  /**
   * Boolean that decides if the Change Category will be displayed.
   */
  @Input() showChangeCategory: boolean | undefined;
  @Input() checkImagesList: TransactionCheckImageResponse | undefined;
  @Input() transactionDetailsLoadingStatus: TransactionsDetailsLoading | undefined;
  @Input() inquireAndDisputeTopics: InquireAndDisputeTopics | undefined;
  @Output() readonly detailsOpen = new EventEmitter<Transaction>();

  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  /**
   * Whether the transaction details are shown or not.
   */
  isDetailsOpen = false;
  /**
   * Whether the notes editing form is shown or not.
   */
  isEditingNotes = false;
  /**
   * Alert details such as visibility and type
   */
  alertOptions = {
    displayAlert: false,
    displayStatus: '',
  };
  /**
   * Show the transaction details.
   */
  openDetails() {
    this.isDetailsOpen = true;

    if (this.transaction) {
      this.detailsOpen.emit(this.transaction);
    }
  }

  /**
   * Hide the transaction details.
   */
  closeDetails() {
    this.isDetailsOpen = false;
    this.onAlertDismiss();
  }

  ngOnInit() {
    if (typeof this.transaction === 'undefined') {
      throw new Error(`"transaction" input is required in "${this.constructor.name}"`);
    }
  }

  showCategory() {
    return this.isPending || !!this.ownCategory;
  }

  get hasModal(): boolean {
    // if the feature is not enabled, we are still displaying modal in the item component
    return !this.featureService.isEnabled(TRANSACTIONS_DETAILS_SHARE_MODAL);
  }

  get isPending(): boolean {
    let isPending = false;
    if (this.transaction && this.transaction.billingStatus) {
      const billingStatus = this.transaction.billingStatus.toUpperCase();
      isPending = billingStatus === BillingStatusEnum.pending || billingStatus === BillingStatusEnum.unbilled;
    }

    return isPending;
  }

  private get ownCategory(): string | undefined {
    return this.transaction && this.transaction.category;
  }

  closeTransactionsCategory(categoryChangeStatus?: string): void {
    if (categoryChangeStatus) {
      this.alertOptions = {
        displayAlert: true,
        displayStatus: categoryChangeStatus,
      };
    } else {
      this.onAlertDismiss();
    }
  }

  onAlertDismiss(): void {
    this.alertOptions = {
      displayAlert: false,
      displayStatus: '',
    };
  }

  constructor(
    private readonly featureService: FeaturesService,
    // eslint-disable-next-line
    @Optional()
    // eslint-disable-next-line
    @Inject(showDetailsConfigToken)
    public showDetailsConfig?: boolean,
  ) {
    if (this.showDetailsConfig === null || this.showDetailsConfig === undefined) {
      this.showDetailsConfig = true;
    }
  }
}
@Directive({
  selector: '[bbTransactionsListItemHeaderCustomizable]',
})
export class TransactionsListItemHeaderCustomizableDirective extends BbTemplate<
  TransactionsListItemComponent,
  undefined
> {}

@Directive({
  selector: '[bbTransactionsListItemCustomizable]',
})
export class TransactionsListItemCustomizableDirective extends BbTemplate<TransactionsListItemComponent, undefined> {}
