import {
  ChangeDetectionStrategy,
  Component,
  Directive,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { AbstractControl, FormControl, FormGroup, Validators } from '@angular/forms';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Observable, of, Subscription } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import {
  AvailableFilterEnum,
  AvailableFilters,
  creditDebitIndicatorEnumValues,
  defaultBillingStatusEnumValues,
  defaultTransactionTypeEnumValues,
  EnumValuesService,
  TransactionsEnumValue,
  TransactionsFilterOptions,
} from '@backbase/transactions-common-ang';

const defaultAllOption = { name: 'All' };

@Component({
  selector: 'bb-transactions-filter',
  templateUrl: './transactions-filter.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsFilterComponent implements OnInit, OnDestroy {
  /**
   * EventEmitter for triggering a filter action.
   */
  @Output() readonly filter = new EventEmitter<TransactionsFilterOptions>();
  /**
   * EventEmitter to hide the filter options
   */
  @Output() readonly closeFilter = new EventEmitter();
  /**
   * EventEmitter with the amount of active filters
   */
  @Output() readonly filterCount = new EventEmitter<number>();
  /**
   * Configuration parameter to show/hide billing status filter
   */
  @Input() hideBillingStatus = false;
  /**
   * Configuration parameter to show/hide filters
   */
  @Input() availableFilters: AvailableFilters = [];

  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;

  readonly availableFilterEnum: typeof AvailableFilterEnum = AvailableFilterEnum;

  transactionsFilterForm = new FormGroup({
    creditDebitIndicator: new FormControl(creditDebitIndicatorEnumValues[0]),
    types: new FormControl(),
    billingStatus: new FormControl(),
    amount: new FormGroup({
      from: new FormControl(undefined, Validators.min(0)),
      to: new FormControl(undefined, Validators.min(0)),
    }),
    date: new FormGroup({
      from: new FormControl(''),
      to: new FormControl(''),
    }),
    categories: new FormControl(),
    checkSerialNumber: new FormGroup({
      from: new FormControl(undefined, Validators.min(0)),
      to: new FormControl(undefined, Validators.min(0)),
    }),
  });

  creditDebitIndicatorOptions: Array<TransactionsEnumValue> = creditDebitIndicatorEnumValues;

  transactionTypeOptions: Observable<Array<TransactionsEnumValue>>;

  billingStatusOptions: Observable<Array<TransactionsEnumValue>>;

  private subscriptions = new Subscription();

  ngOnInit() {
    this.subscriptions.add(this.transactionTypeOptions.subscribe(this.setDefaultAllOption('types')));

    this.subscriptions.add(this.billingStatusOptions.subscribe(this.setDefaultAllOption('billingStatus')));

    this.subscriptions.add(
      this.serialNumberFormGroup?.valueChanges.subscribe(() => {
        this.fromSerialNumber?.setValidators(Validators.max(this.toSerialNumber?.value));
        this.toSerialNumber?.setValidators(Validators.min(this.fromSerialNumber?.value || 0));
        this.fromSerialNumber?.updateValueAndValidity({ emitEvent: false });
        this.toSerialNumber?.updateValueAndValidity({ emitEvent: false });
      }),
    );

    this.subscriptions.add(
      this.amountFormGroup?.valueChanges.subscribe(() => {
        this.fromAmount?.setValidators(Validators.max(this.toAmount?.value));
        this.toAmount?.setValidators(Validators.min(this.fromAmount?.value || 0));
        this.fromAmount?.updateValueAndValidity({ emitEvent: false });
        this.toAmount?.updateValueAndValidity({ emitEvent: false });
      }),
    );
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  compareByValue(optionListedValue: any, placeholderValue: any) {
    if (!optionListedValue || !placeholderValue) {
      return;
    }

    return optionListedValue.value === placeholderValue.value;
  }

  private readonly setDefaultAllOption = (field: string) => (data: any) => {
    const control = this.transactionsFilterForm.get(field);
    if (control) {
      control.setValue(data[0]);
    }
  };

  /**
   * Emit event to apply filter.
   *
   * @param filterParams - Options to filter transactions list with.
   */
  onFilter(filterParams: TransactionsFilterOptions) {
    this.filterCount.emit(this.activeFilterCounter);
    this.filter.emit(filterParams);
  }

  /**
   * Checks all the form fields and looks for differences with a default model to define if a filter was applied or not
   */
  private get activeFilterCounter(): number {
    const fields = [
      this.fromDate,
      this.toDate,
      this.fromAmount,
      this.toAmount,
      this.fromSerialNumber,
      this.toSerialNumber,
      this.creditDebitIndicator,
      this.transactionType,
      this.billingStatus,
    ];
    const activeFilters = fields.reduce((count, field) => (this.isFilterApplied(field) ? ++count : count), 0);

    return activeFilters;
  }

  private isFilterApplied(control: AbstractControl | null | undefined) {
    if (typeof control?.value === 'object' && control.value !== null) {
      return JSON.stringify(control.value) !== JSON.stringify(defaultAllOption);
    } else {
      return control?.valid && (!!control.value || control.value === 0);
    }
  }

  /**
   * Clear filter form and reset values to default.
   */
  onClearFilter() {
    this.transactionsFilterForm.reset({
      creditDebitIndicator: creditDebitIndicatorEnumValues[0],
      types: defaultAllOption,
      billingStatus: defaultAllOption,
      date: {
        from: '',
        to: '',
      },
    });

    this.transactionsFilterForm.markAsPristine();
    this.onFilter({});
  }

  /**
   * Emit event to close this filter form.
   */
  onCloseFilter() {
    this.closeFilter.emit();
  }

  private get dateFormGroup() {
    return this.transactionsFilterForm.get('date');
  }

  get fromDate() {
    return this.dateFormGroup?.get('from');
  }

  get toDate() {
    return this.dateFormGroup?.get('to');
  }

  private get amountFormGroup() {
    return this.transactionsFilterForm.get('amount');
  }

  private get fromAmount() {
    return this.amountFormGroup?.get('from');
  }

  private get toAmount() {
    return this.amountFormGroup?.get('to');
  }

  private get serialNumberFormGroup() {
    return this.transactionsFilterForm.get('checkSerialNumber');
  }

  private get fromSerialNumber() {
    return this.serialNumberFormGroup?.get('from');
  }

  private get toSerialNumber() {
    return this.serialNumberFormGroup?.get('to');
  }

  private get creditDebitIndicator() {
    return this.transactionsFilterForm.get('creditDebitIndicator');
  }

  private get transactionType() {
    return this.transactionsFilterForm.get('types');
  }

  private get billingStatus() {
    return this.transactionsFilterForm.get('billingStatus');
  }

  formatDateToString(dateArg?: string | null | undefined) {
    if (!!dateArg && new Date(dateArg).getTime()) {
      const argDate = new Date(dateArg);
      const formatedArgDate = new Date(argDate.getFullYear(), argDate.getMonth(), argDate.getDate());

      return formatedArgDate.toISOString();
    }

    return;
  }

  isFieldValid(fieldGroup: string, fieldName: string): boolean {
    const fieldControl = this.transactionsFilterForm.get(fieldGroup)?.get(fieldName);

    return !!fieldControl?.valid || !!fieldControl?.untouched;
  }

  constructor(private readonly enumValuesService: EnumValuesService) {
    const insertDefaultValue = (data: Array<TransactionsEnumValue>) => [defaultAllOption, ...data];

    this.transactionTypeOptions = this.enumValuesService.getEnumValues('type').pipe(
      map(insertDefaultValue),
      catchError(() => of(defaultTransactionTypeEnumValues)),
    );

    this.billingStatusOptions = this.enumValuesService.getEnumValues('billingStatus').pipe(
      map(insertDefaultValue),
      catchError(() => of(defaultBillingStatusEnumValues)),
    );
  }
}

@Directive({
  selector: '[bbTransactionsListFilterCustomizable]',
})
export class TransactionsListFilterCustomizableDirective extends BbTemplate<TransactionsFilterComponent, undefined> {}
